import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path: '/Home',
      name: 'home',
      component:() => import('@/components/pages/home')
    },
    {
      path: '/',
      name: 'blog',
      component:() => import('@/components/pages/blog')
    }
  ]
})



